<template>
    <div>
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick"
            style="height: 40px; background-color: white; border-top: 1px solid #e6e6e6;">
            <el-tab-pane label="企业数据" name="first">
            </el-tab-pane>
            <el-tab-pane label="服务器数据" name="second"></el-tab-pane>
            <el-tab-pane label="省份数据" name="third"></el-tab-pane>
        </el-tabs>
        <div class="router-v">
            <router-view></router-view>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            activeName: 'first'
        }
    },
    methods: {
        handleClick(tab) {
            let targetPath = '';
            switch (tab.name) {
                case 'first':
                    targetPath = '/enterprisedata';
                    break;
                case 'second':
                    targetPath = '/Serverdata';
                    break;
                case 'third':
                    targetPath = '/Provdata';
                    break;
                default:
                    return; // 如果没有匹配的选项，直接返回不做任何操作
            }
            // 检查目标路径是否与当前路径相同
            if (this.$route.path !== targetPath) {
                // 如果不同，则进行跳转
                this.$router.push(targetPath);
            } else {
                // 如果相同，打印消息而不执行跳转
                console.log('已经是', tab.label, '页面，无需再次跳转。');
            }
        }
    }
}
</script>
<style>
.router-v{
  padding: 20px;
}
</style>